<template>
  <!--  -->
  <div class="page align-around">
    <div class="name justify-center">
      <img src="~@/assets/luckyTurn/name@2x.png" alt="" />
    </div>
    <div class="tabs justify-center">
      <div class="tab-top">
        <div
          :class="['tabi', levelActive === 1 ? 'taba' : '']"
          @click="changeLevel(1)"
        >
          9/
        </div>
        <div class="split justify-center align-center">
          <img src="../assets/luckyTurn/split@2x.png" alt="" />
        </div>
        <div
          :class="['tabi', levelActive === 2 ? 'taba' : '']"
          @click="changeLevel(2)"
        >
          99/
        </div>
        <div class="split justify-center align-center">
          <img src="../assets/luckyTurn/split@2x.png" alt="" />
        </div>
        <div
          :class="['tabi', levelActive === 3 ? 'taba' : '']"
          @click="changeLevel(3)"
        >
          999/
        </div>
      </div>
      <div class="tab-line">
        <div class="line-bg">
          <div :class="['line', levelActive === 1 ? 'line-active' : '']"></div>
          <div class="dot"></div>
          <div :class="['line', levelActive === 2 ? 'line-active' : '']"></div>
          <div class="dot"></div>
          <div :class="['line', levelActive === 3 ? 'line-active' : '']"></div>
        </div>
      </div>
    </div>
    <div class="right-btn">
      <div class="btn" @click="viewNotice"><br /></div>
      <div class="btn" @click="viewRecord"><br /></div>
    </div>
    <div class="content">
      <div class="turn justify-center">
        <LuckyWheel
          ref="myLucky"
          width="350px"
          height="350px"
          :prizes="prizes"
          :blocks="blocks"
          :buttons="buttons"
          @start="beforeStart"
          @end="endCallback"
        />
      </div>
      <div class="leg justify-center">
        <img src="../assets/luckyTurn/zhijia@2x.png" class="zhijia" />
      </div>
      <div class="gu">
        <img src="~@/assets/luckyTurn/gu@2x.png" />
        <div class="total">
          <div class="box">
            <span class="red">{{ maxStar }}</span>
          </div>
        </div>
        <div class="tips">
          <div class="text">{{ joins }}</div>
          <div class="text">
            {{ `${chances_left + chances_free}` }}
            <span v-show="chances_free > 0"
              >({{ chances_free }})</span
            >
          </div>
        </div>
      </div>
    </div>

    <Modal class="modal" :visible.sync="isShowNotice" title="">
      <div class="modal-content" slot="content">
        <!-- <div class="text mb-24"></div> -->
        <div ref="rule" class="sub-text mb-32 rule-lh tal" v-html="rule"></div>
      </div>
    </Modal>
    <!--  -->
    <Modal
      class="modal modal-record"
      :visible.sync="isShowRecord"
      title=""
    >
      <div
        class="modal-content"
        slot="content"
        v-infinite-scroll="loadMore"
        infinite-scroll-disabled="loading"
        infinite-scroll-distance="40"
      >
        <div class="item" v-for="(item, index) in recordList" :key="index">
          <div class="tit">
            <div>{{ item.title }}</div>
            <div>{{ item.cost === 0 ? "" : "-" + item.cost }}</div>
          </div>
          <div class="time">{{ item.create_time }}</div>
        </div>
        <!-- <divider :visible.sync="loading" :type="'text'" :title="'end'" /> -->
        <divider :visible.sync="loading" :type="'spin'" />
      </div>
    </Modal>

    <Modal class="modal" :visible.sync="isModalSpend" title="">
      <div class="modal-content" slot="content">
        <div class="text mb-24">
          {{
            levelActive == 1 ? "9" : levelActive === 2 ? "99" : "999"
          }}
        </div>
      </div>
      <div class="modal-footer-row-two" slot="footer">
        <img
          @click="cancel"
          src="@/assets/images/common/modal_btn_qx.png"
          alt=""
        />
        <img
          @click="confirm2luckdraw"
          src="@/assets/images/common/modal_btn_qd.png"
          alt=""
        />
      </div>
    </Modal>

    <Modal
      class="modal"
      :visible.sync="isModalResult"
      :title="isGetPrize ? '' : ''"
    >
      <div class="modal-content" slot="content">
        <div class="text mb-24">
          <span v-show="isGetPrize"></span>{{ curPrize }}
        </div>
        <div v-show="isGetPrize" class="sub-text mb-32">
          
        </div>
        <div v-show="!isGetPrize" class="text mb-24">~</div>
      </div>
      <div class="modal-footer" slot="footer">
        <img
          @click="confirmResult"
          src="@/assets/images/common/modal_again.png"
          alt=""
        />
      </div>
      <div class="modal-footer" slot="footer">
        <img
          @click="cancelResult"
          src="@/assets/images/common/modal_iknow_gray.png"
          alt=""
        />
      </div>
    </Modal>

    <!--  -->
    <Modal class="modal" :visible.sync="isShowFaild" title="">
      <div class="modal-content" slot="content">
        <div class="text mb-24">
          {{ faildType === 1 ? "" : "" }}
        </div>
      </div>
      <div class="modal-footer" slot="footer">
        <img
          v-if="faildType === 1"
          @click="goto('home')"
          src="@/assets/images/common/modal_btn_earn.png"
          alt=""
        />
        <img
          v-else
          @click="cancelFaild"
          src="@/assets/images/common/modal_btn_zdl.png"
          alt=""
        />
      </div>
    </Modal>

    <!--  -->
    <Modal class="modal" :visible.sync="isModalFree" title="">
      <div class="modal-content" slot="content">
        <div class="text mb-24">1</div>
      </div>
      <div class="modal-footer" slot="footer">
        <img
          @click="confirmFree2luckdraw"
          src="@/assets/images/common/modal_use.png"
        />
      </div>
      <div class="modal-footer" slot="footer">
        <img
          @click="userStar2Luckdraw"
          src="@/assets/images/common/modal_have.png"
          alt=""
        />
      </div>
    </Modal>
  </div>
</template>

<script>
import Vue from 'vue'
import VueLuckyCanvas from '@lucky-canvas/vue'
Vue.use(VueLuckyCanvas)
let prizeImg = {
  starImg: [{
    src: './static/img/luckyTurn/star@2x.png',
    width: '35%',
    top: '40%'
  }],
  giftImg: [{
    src: './static/img/luckyTurn/prize@2x.png',
    width: '35%',
    top: '40%'
  }],
  goldImg: [{
    src: './static/img/luckyTurn/prize4@2x.png',
    width: '35%',
    top: '40%'
  }],

  addOneImg: [{
    src: './static/img/luckyTurn/prize3@2x.png',
    width: '35%',
    top: '40%'
  }],
  noneImg: [{
    src: './static/img/luckyTurn/prize_none@2x.png',
    width: '35%',
    top: '40%'
  }]
}

export default {
  components: {
  },
  data () {
    return {
      userInfo: JSON.parse(localStorage.getItem('userInfo')).data,
      curStarCount: JSON.parse(localStorage.getItem('userInfo')).data.available,
      levelCount0: this.$store.getters.userInfo.levelCount0, // level0 
      levelCount1: this.$store.getters.userInfo.levelCount1, // level1 
      levelCount2: this.$store.getters.userInfo.levelCount2, // level2 
      levelActive: 1,
      levelList: [
        {
          title: '9/',
          id: 9
        },
        {
          title: '99/',
          id: 99
        },
        {
          title: '999/',
          id: 999
        }
      ],
      isShowNotice: false, // 
      isShowRecord: false, // 
      isModalSpend: false, // 
      isModalFree: false, // 
      isModalResult: false, // 
      isShowFaild: false, // 
      faildType: 1, // 1  2
      isShowSort: false,
      hasFree: true, // 
      curPrize: '', // text
      isGetPrize: false, // 
      blocks: [
        { padding: '10px' },
        {
          padding: '26px',
          imgs: [{
            src: './static/img/luckyTurn/lucky_bg@2x.png',
            width: '100%',
            rotate: true
          }]
        }
      ],
      prizes: [
        {
          fonts: [{ text: '+5', fontSize: '14px', fontColor: '#fff', top: '15%' }],
          imgs: prizeImg.starImg
        },
        {
          fonts: [{ text: '+10', fontSize: '14px', fontColor: '#fff', top: '15%' }],
          imgs: prizeImg.starImg
        },
        {
          fonts: [{ text: '+30', fontSize: '14px', fontColor: '#fff', top: '15%' }],
          imgs: prizeImg.starImg
        },
        {
          fonts: [{ text: '+50', fontSize: '14px', fontColor: '#fff', top: '15%' }],
          imgs: prizeImg.starImg
        },
        { fonts: [{ text: '+1', fontSize: '14px', fontColor: '#fff', top: '15%' }], imgs: prizeImg.addOneImg },
        { fonts: [{ text: '', fontSize: '14px', fontColor: '#fff', top: '15%' }], imgs: prizeImg.noneImg }
      ],
      buttons: [
        {
          radius: '50%',
          imgs: [{
            src: './static/img/luckyTurn/circle@2x.png',
            width: '100%',
            top: '-110%'
          }],
          fonts: [{ text: '\n', top: '-20px', fontColor: '#fff' }]
        }
      ],
      maxStar: 0, // 
      joins: 0, //  
      chances_left: 0, //  
      chances_free: 0, //  
      use_free: '0', // 10 

      recordList: [
        {
          title: '100',
          cost: '9',
          time: '2022-03-05 13:09:23',
          id: 1
        },
        {
          title: '',
          cost: '9',
          time: '2022-03-05 13:09:23',
          id: 7
        },
        {
          title: '+1',
          cost: '9',
          time: '2022-03-05 13:09:23',
          id: 6
        }

      ],
      page_num: 1,
      page_size: 10,
      loading: false,
      is_last_page: false, // 
      rule: '' // 
    }
  },
  created () {
    this.recordList = []
    this.getLuckySets(this.levelActive)
    this.getCharges(this.levelActive)
  },
  methods: {
    changeLevel (level) {
      this.levelActive = level
      console.log(level)
      // if (level === 0) {
      //   this.prizes = this.prizes1
      // } else if (level === 1) {
      //   this.prizes = this.prizes2
      // } else {
      //   this.prizes = this.prizes3
      // }
      this.maxStar = ''
      this.getLuckySets(level)
      this.getCharges(level)
    },
    goList () {
      this.$router.push('invitationList')
    },
    goto (name) {
      if (name === 'home') {
        this.$router.push({ name: name, params: { earnStar: '1' } })
      }
    },
    // star
    // 
    beforeStart () {
      if (this.chances_free > 0) {
        this.isModalFree = true
        this.use_free = 0
      } else {
        this.isModalSpend = true
      }
    },
    // 
    async doLottery () {
      // 
      this.curPrize = ''
      this.isGetPrize = false
      console.log(this.$store.getters.userInfo)

      // play
      this.$refs.myLucky.play()
      let res = await this.$request('get', 'user/dolottery', {
        type: this.levelActive,
        uid: this.userInfo.uid,
        use_free: this.use_free // 1
      })
      if (res && res.error_no === '0' && res.data) {
        console.log('apires.data')
        console.log(res.data)
        if (res.data.type === '2') {
          this.isGetPrize = false
        } else {
          this.isGetPrize = true
        }

        setTimeout(() => {
          // stop
          this.$refs.myLucky.stop(res.data.id - 1 || 5)
        }, 2000)
      }
    },
    // 
    confirm2luckdraw () {
      this.isModalSpend = false
      this.use_free = '0'
      // 
      if ((this.levelActive === 1 && this.curStarCount < 9) ||
        (this.levelActive === 2 && this.curStarCount < 99) ||
        (this.levelActive === 3 && this.curStarCount < 999)
      ) {
        this.faildType = 1
        this.isShowFaild = true
      } else if (this.chances_free < 1 && this.chances_left < 1) {
        // 
        this.faildType = 2
        this.isShowFaild = true
      } else {
        this.doLottery()
      }
    },
    // 
    confirmFree2luckdraw () {
      this.isModalFree = false
      this.use_free = '1'
      this.doLottery()
    },
    // cancel
    cancel () {
      this.isModalSpend = false
    },
    // cancel
    cancelResult () {
      this.isModalResult = false
    },
    // cnacel
    cancelFaild () {
      this.isShowFaild = false
    },
    confirmResult () {
      this.cancelResult()
      this.confirm2luckdraw()
    },
    // 
    userStar2Luckdraw () {
      this.isModalFree = false

      this.use_free = '0'
      if (this.chances_left === 0) {
        this.faildType = 2
        this.isShowFaild = true
      } else {
        this.faildType = 1
        this.isModalSpend = true
      }
    },
    // end
    endCallback (prize) {
      console.log('')
      console.log(prize)
      let curp = prize.fonts[0].text
      if (curp.startsWith('+')) { // 
        this.curPrize = curp.slice(1)
      } else {
        this.curPrize = curp
      }
      // 
      this.getCharges(this.levelActive)
      this.getAccount()
      this.isModalResult = true
    },
    viewNotice () {
      this.getRule()
      // this.isShowNotice = true
    },
    async viewRecord () {
      this.isShowRecord = true
      this.page_num = 1
      this.getRecord()
    },
    // 
    async getRecord () {
      // this.isShowRecord = true
      // 
      let res = await this.$request('get', 'user/lotterypagelist', {
        page_num: this.page_num,
        page_size: this.page_size,
        uid: this.userInfo.uid
      })
      if (res && res.error_no === '0') {
        this.loading = false
        if (this.page_num === 1) {
          this.recordList = res.data.list
        } else {
          this.recordList = this.recordList.concat(res.data.list)
        }
        if (this.page_num * this.page_size >= res.data.total) {
          this.is_last_page = true
        } else {
          this.is_last_page = false
        }
      }
    },
    // 
    loadMore () {
      if (!this.is_last_page) {
        this.loading = true
        this.page_num += 1
        this.getRecord()
      }
      // setTimeout(() => {
      //   for (let i = 1; i <= 10; i++) {
      //     let last = {
      //       title: '100',
      //       cost: '9',
      //       time: '2022-03-05 13:09:23'
      //     }
      //     this.recordList.push(last)
      //   }
      //   this.loading = false
      // }, 1500)
    },
    // 
    async getRule () {
      let res = await this.$request('get', 'activity/getrule', { type: 2 })
      if (res && res.error_no === '0') {
        this.rule = res.data
        // this.rule = `<p>
        //   1 
        //   
        // </p>
        // <p class="pcenter">
        //   2 
        //   5g
        // </p>
        // <p>
        //   3 
        //   110285g915g
        // </p>`
        this.isShowNotice = true
      }
    },
    // 
    async getLuckySets (type) {
      // this.isShowRecord = true
      // 
      let res = await this.$request('get', 'activity/getlotterylist', {
        type: type
      })
      if (res && res.error_no === '0' && res.data && res.data.length) {
        // console.log(res)
        res.data.forEach((ele, index) => {
          let ptext, img

          // 1-2-3-4-AI
          if (ele.type === '1') {
            ptext = '+' + ele.title
            img = prizeImg.starImg
            this.maxStar = Math.max(this.maxStar, ele.worth)
          } else if (ele.type === '2') {
            ptext = ele.title
            img = prizeImg.noneImg
          } else if (ele.type === '3') {
            ptext = ele.title
            // ptext = '+1'
            img = prizeImg.addOneImg
          } else if (ele.type === '4') {
            ptext = ele.title
            img = prizeImg.goldImg
          }
          this.prizes[index].fonts[0].text = ptext
          this.prizes[index].imgs = img
        })
      }
    },
    // 
    async getCharges (type) {
      // this.isShowRecord = true
      // 
      let res = await this.$request('get', 'user/getlotteryinfo', {
        type: type,
        uid: this.userInfo.uid
      })
      if (res && res.error_no === '0' && res.data) {
        this.chances_free = res.data.chances_free
        this.chances_left = res.data.chances_left
        this.joins = res.data.joins
      }
    },
    // 
    async getAccount (type) {
      let res = await this.$request('get', 'user/getaccount', {
        uid: this.userInfo.uid
      })
      if (res && res.error_no === '0' && res.data) {
        this.$store.commit('setUserInfo', res.data)
      }
    }
  }
}
</script>

<style lang="less" scoped>
