    // background: pink;
    .bg-image("~@/assets/luckyTurn/tabs","png");
    .flex_func(column, center,center,nowrap);
    .tab-top {
      width: 100%;
      height: 50px;
      padding: 0 34px;
      .flex_func(row, space-between,center,nowrap);
      .tabi {
        width: 162px;
        height: 50px;
        line-height: 50px;
        text-align: center;
        border-radius: 25px;
