        img {
          width: 12px;
          height: 21px;
        }
      }
    }
    .tab-line {
      height: 12px;
      // width: calc(100% - 74px);
      padding: 0 37px;
      width: 100%;
      margin-top: 7px;
      .line-bg {
        width: 100%;
        height: 100%;
        .bg-image("../assets/luckyTurn/line_bg","png");
        .flex_func(row, space-between,center,nowrap);
        .line {
          // width: calc(33.33% - 8px);
          width: 33.33%;
          height: 100%;
          // background: greenyellow;
        }
        .line-active {
          .bg-image("../assets/luckyTurn/line_active","png");
        }
        .dot {
          width: 12px;
